<template>
    <v-dialog v-model="dialog" max-width="600">
        <template v-slot:activator="{ props: activatorProps }">
            <v-btn
                variant="outlined"
                class="justify-right"
                rounded="lg"
                elevation="4"
                :size="size"
                v-bind="activatorProps"
                block>
                <template v-slot:append>
                    <v-icon :size="size" icon="mdi-email" class="ml-1"></v-icon>
                </template>
                <template v-slot:default>
                    <div
                        class="playfair-display"
                        :class="{
                            'text-h5': $vuetify.display.mdAndUp,
                            'text-uppercase': $vuetify.display.mdAndUp,
                            'text-button': $vuetify.display.smAndDown,
                        }">
                        Inqiure about the price
                    </div>
                </template>
            </v-btn>
        </template>

        <v-card
            prepend-icon="mdi-email"
            title="Inqiure about the price and available dates."
            width="100%"
            class="justify-center">
            <template v-slot:title>
                <p class="text-wrap">
                    Inqiure about the price and available dates.
                </p>
            </template>
            <template v-slot:subtitle>
                <p class="text-wrap">
                    You can send your inquiry to us using the form below. We
                    will get back to you shortly via email.
                </p>
            </template>
            <v-card-text>
                <v-row dense>
                    <v-col cols="12" md="6" sm="6">
                        <v-text-field
                            label="First name*"
                            required></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                        <v-text-field
                            label="Last name*"
                            required></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                        <v-text-field label="Email*" required></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                        <v-select
                            :items="['1', '2', '3', '4', '5', '6', '7', '8']"
                            label="Number of people*"
                            required></v-select>
                    </v-col>

                    <v-col cols="12" sm="12" md="6">
                        <v-menu :close-on-content-click="false">
                            <template v-slot:activator="{ props }">
                                <v-text-field
                                    v-bind="props"
                                    readonly
                                    required
                                    v-model="formattedDateFrom"
                                    label="Select starting date"></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="dateFrom"
                                color="green"
                                hide-header></v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="12" sm="12" md="6">
                        <v-menu :close-on-content-click="false">
                            <template v-slot:activator="{ props }">
                                <v-text-field
                                    v-bind="props"
                                    readonly
                                    required
                                    v-model="formattedDateTo"
                                    label="Select ending date"></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="dateTo"
                                color="green"
                                hide-header></v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                        <v-textarea
                            label="Additional information or requests"></v-textarea>
                    </v-col>
                </v-row>

                <small class="text-caption text-medium-emphasis">
                    *indicates required field
                </small>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
                <v-spacer></v-spacer>

                <v-btn
                    text="Close"
                    variant="plain"
                    @click="dialog = false"></v-btn>

                <v-btn
                    color="green"
                    text="Send"
                    variant="tonal"
                    @click="dialog = false"></v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import { defineComponent } from "vue";

// Components
export default defineComponent({
    name: "ContactFormCard",

    components: {},
    data: () => ({
        dialog: false,
        dateFromPicker: false,
        dateFrom: new Date(),
        dateTo: new Date(),
    }),
    computed: {
        size() {
            if (this.$vuetify.display.mdAndUp) {
                return "large";
            } else {
                return "medium";
            }
        },
        formattedDateFrom() {
            return this.dateFrom.toLocaleDateString("en-GB");
        },
        formattedDateTo() {
            return this.dateTo.toLocaleDateString("en-GB");
        },
    },
});
</script>
<style scoped>
.width-600 {
    width: 600px !important;
}
.width-300 {
    width: 300px;
}
.height-400 {
    height: 400px;
}
.height-200 {
    height: 200px;
}
